body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.card {
  background: -webkit-gradient(linear, left top, left bottom, from(#FFFFFF), to(#F6F6F6));
  margin: auto;
  margin-bottom: 1em;
  margin-top: 1em;
  width: 18rem;
  height: 18rem;
  text-align: left;
  overflow: hidden;
}

.card-title {
  margin-top: 0.2rem;
  margin-bottom: 0.2rem;
}

.card-header {
  white-space: nowrap;
  font-family: Arial Helvetica, Sans-Serif;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  font-weight: normal !important;
  padding: 10px 10px 10px 15px !important;
  background-color: #B5975B !important;
  border-color: #B5975B !important;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  line-height: 14px;
  color: #fff;
  margin: 0;
  font-size: 18px !important;
  text-shadow: #999 0.1em 0.1em 0.2em;
  min-height: 17px;
  overflow: hidden;
}

.card-header h5 {
  font-size: 18px !important;
  text-shadow: #999 0.1em 0.1em 0.2em;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
}

.address {
  margin-top: 0.5em;
}

.address > p {
  margin-bottom: 0;
  font-size: 80%;
  color: #ababab;
}

.form-input {
  margin-top: 1em;
}

.form-row {
  margin: auto;
}


